html {
    background-color:#fff;
}

body {
    font: 100% 'Arcade Normal';
    background-color: #000;
}
.top {
    margin-top:5%;
}
.container {
    background-color: #fff;
    color: #192a56;
}
.title {
    font-family: 'Stellar';
    font-weight: bold;
    font-style: normal;
    text-transform: uppercase;
    margin: auto;
    color: #192a56;
}
.tabletitle {
    font-family: 'Stellar';
    font-style: normal;
    text-transform: uppercase;
    color: #192a56;
}

.vw {
    margin-top: 5%;
}

@font-face {
    font-family: 'Stellar';
    src: url('Stellar-Light.woff2') format('woff2'),
        url('Stellar-Light.woff') format('woff');
    font-weight: 200;
    font-style: normal;
}

@font-face {
    font-family: 'Stellar';
    src: url('Stellar-Bold.woff2') format('woff2'),
        url('Stellar-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}




